import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyPaginatorModule as MatPaginatorModule } from '@angular/material/legacy-paginator';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatSortModule } from '@angular/material/sort';
import { RouterModule } from '@angular/router';
import { ResponsiveTableHeightModule } from '@tei/core';
import { BreadCrumb, BreadCrumbModule, ConfirmDialogModule } from '@tei/material';
import { SettingsCardLayoutModule } from '@tei/theme';
import { MasterListService } from '../../@services/master-list.service';
import { MasterListComponent } from './master-list.component';
import { MasterListRoutingModule } from './master-list-routing.module';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule, RouterModule];
const MATERIAL_MODULES = [MatCardModule, MatDividerModule, MatButtonModule, MatSelectModule, MatInputModule,
  MatIconModule, MatTableModule, MatPaginatorModule, MatSortModule, MatMenuModule, BreadCrumbModule];
const TEI_MATERIAL_MODULES = [ConfirmDialogModule, SettingsCardLayoutModule];
const TEI_CORE_MODULES = [ResponsiveTableHeightModule];
const MODULES = [MasterListRoutingModule];
@NgModule({
  declarations: [MasterListComponent],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...MODULES
  ],
  providers: [
    { provide: 'IMasterListService', useClass: MasterListService }
  ],
})
export class MasterListModule { }
