import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
import { ActivatedRoute, Router } from '@angular/router';
import { ResponsiveService } from '@tei/core';
import { ConfirmDialogService, DialogData, SnackBarService } from '@tei/material';
import { baseRoute } from '../../@intermediate/constants/configs';
import
{
  FieldGroupMasterField, FieldGroupMasterFieldMetadata, GetManageFieldGroupRequest,
  GetManageFieldGroupResponse, IManageFieldGroupService, MasterField,
  PostManageFieldGroupRequest, PostManageFieldGroupRequestForm, SCOPE
} from '../../@intermediate/manage-field-group';

@Component({
  selector: 'tei-field-management-pages-manage-field-group',
  templateUrl: './manage-field-group.component.html',
  styleUrls: ['./manage-field-group.component.scss']
})
export class ManageFieldGroupComponent implements OnInit
{
  model: GetManageFieldGroupResponse;
  form: PostManageFieldGroupRequestForm;
  fieldMetadata = new FieldGroupMasterFieldMetadata();
  datasource_fields: MatTableDataSource<FieldGroupMasterField>;
  filteredMasterFields: MasterField[];
  readonly scope: any = SCOPE;

  manageFieldBaseRoute = baseRoute;

  fieldGroupID: number;
  masterField: MasterField;
  allowCreate: boolean;
  allowEdit: boolean;
  filedGroupTypeDisabled = false;
  isMobile: boolean;
  isTablet: boolean;

  constructor(
    @Inject('IManageFieldGroupService') private ManageFieldGroupService: IManageFieldGroupService,
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private responsiveService: ResponsiveService,
    private confirmdialogService: ConfirmDialogService,
    private snackbarService: SnackBarService)
  {
    activatedRoute.params.subscribe(params =>
    {
      if (params['field-group-id'] !== '')
      {
        this.fieldGroupID = params['field-group-id'];
        this.filedGroupTypeDisabled = true;
      }

    });
  }

  ngOnInit()
  {
    this.form = new PostManageFieldGroupRequestForm(this.fieldGroupID === undefined);
    const request = new GetManageFieldGroupRequest();

    if (this.fieldGroupID)
    {
      request.FieldGroupID = this.fieldGroupID;
    }
    this.getMobileSize();
    this.load(request);
  }

  private load(request: GetManageFieldGroupRequest)
  {
    this.model = new GetManageFieldGroupResponse();
    this.ManageFieldGroupService.Get(request).subscribe(response =>
    {
      this.model = response;
      if (this.model.SelectedFieldGroupTypeID === 0)
      {
        this.model.FieldGroupTypes = this.model.FieldGroupTypes.filter(f => f.AllowCreate === true);
        this.allowCreate = true;
      }
      else
      {
        this.allowCreate = this.model.FieldGroupTypes.find(f => f.FieldGroupTypeID == this.model.SelectedFieldGroupTypeID).AllowCreate;
        this.allowEdit = this.model.FieldGroupTypes.find(f => f.FieldGroupTypeID == this.model.SelectedFieldGroupTypeID).AllowEdit;
      }
      this.bindForm(this.model);
    });
  }

  private bindForm(response: GetManageFieldGroupResponse)
  {
    this.form.setValue(response);
    this.bindFields(this.model.FieldGroupMasterFields);
  }

  private bindFields(fieldGroupMasterFields: FieldGroupMasterField[])
  {
    this.datasource_fields = new MatTableDataSource<FieldGroupMasterField>(fieldGroupMasterFields);
  }
  getMobileSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isMobile = screenSize === 'xs';
    this.isTablet = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
  }
  filterMasterFields(value: string)
  {
    this.filteredMasterFields = value && value.length >= 1 ? this._filterMasterFields(value) : [];
  }

  private _filterMasterFields(value: string): MasterField[]
  {
    return this.model.MasterFields.filter(masterField => masterField.Title.toLowerCase().includes(value.toLowerCase()) &&
      !this.model.FieldGroupMasterFields.find(f => f.MasterFieldID === masterField.MasterFieldID || f.MasterFieldRootMasterFieldID === masterField.MasterFieldID));
  }

  displayMasterField(masterField?: MasterField): string | undefined
  {
    return masterField ? masterField.Title : undefined;
  }

  manageField(masterField)
  {
    if (masterField)
    {
      this.addField(masterField);
      this.bindFields(this.model.FieldGroupMasterFields);
    }

    this.masterField = null;
  }

  private addField(masterField: MasterField)
  {
    const fieldGroupMasterField = new FieldGroupMasterField();

    fieldGroupMasterField.FieldGroupMasterFieldID = null;
    fieldGroupMasterField.MasterFieldID = masterField.MasterFieldID;
    fieldGroupMasterField.MasterFieldTypeName = masterField.TypeName;
    fieldGroupMasterField.MasterFieldTitle = masterField.Title;
    fieldGroupMasterField.MasterFieldLabel = masterField.Label;
    fieldGroupMasterField.Scope = SCOPE.Person;
    fieldGroupMasterField.Required = true;
    fieldGroupMasterField.SaveEntry = true;
    fieldGroupMasterField.HideInEmail = false;
    fieldGroupMasterField.UseDefault = true;

    this.model.FieldGroupMasterFields.push(fieldGroupMasterField);
    this.form.addMasterField(fieldGroupMasterField);
  }

  moveFieldUp(index: number)
  {
    this.moveField(index, index - 1);
  }

  moveFieldDown(index: number)
  {
    this.moveField(index, index + 1);
  }

  private moveField(from: number, to: number)
  {
    this.moveArrayItem(this.form.FieldGroupMasterFields.controls, from, to);
    this.moveArrayItem(this.model.FieldGroupMasterFields, from, to);
    this.bindFields(this.model.FieldGroupMasterFields);
  }

  deleteField(index: number)
  {
    const fieldGroupMasterField = this.model.FieldGroupMasterFields[index];

    const dialogData: DialogData = {
      title: 'Delete',
      message: `Delete the ${ fieldGroupMasterField.MasterFieldTitle } field?`,
      textOkayButton: 'Delete',
      textCancelButton: 'Cancel'
    };

    this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.form.FieldGroupMasterFields.controls.splice(index, 1);
        this.form.FieldGroupMasterFields.updateValueAndValidity();

        this.model.FieldGroupMasterFields.splice(index, 1);
        this.bindFields(this.model.FieldGroupMasterFields);
      }
    });
  }

  private moveArrayItem(array: any[], from: number, to: number)
  {
    array.splice(to, 0, array.splice(from, 1)[0]);
  }

  save(form: PostManageFieldGroupRequestForm)
  {
    const request = form.getRawValue() as PostManageFieldGroupRequest;

    this.ManageFieldGroupService.Post(request).subscribe(
      response =>
      {
        this.snackbarService.showSuccess(`${ request.Name } saved successfully.`);
        this.router.navigate([baseRoute + '/booking-fields/field-groups'], { relativeTo: this.activatedRoute });
      },
      ex =>
      {
        this.snackbarService.showError(ex.error.Message);
      });
  }
}
