import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { MatLegacyPaginator as MatPaginator } from '@angular/material/legacy-paginator';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
import { MatSort } from '@angular/material/sort';
import { ActivatedRoute, Router } from '@angular/router';
import { ResponsiveService } from '@tei/core';
import { BreadCrumb, ConfirmDialogService, DialogData } from '@tei/material';
import { DeleteFieldGroupRequest, DuplicateFieldGroupRequest, FieldGroup, FieldGroupMetadata, GetFieldGroupsRequest, GetFieldGroupsRequestForm, GetFieldGroupsResponse, IFieldGroupsService } from '../../@intermediate/field-groups';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'tei-field-management-pages-field-groups',
  templateUrl: './field-groups.component.html',
  styleUrls: ['./field-groups.component.scss']
})
export class FieldGroupsComponent implements OnInit
{
  model: GetFieldGroupsResponse;
  metadata = new FieldGroupMetadata();
  form_search: GetFieldGroupsRequestForm;
  datasource: MatTableDataSource<FieldGroup>;

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  isMobile: boolean;
  isTablet: boolean;
  isSmallScreen: boolean;
  isMediumScreen: boolean;

  constructor(
    @Inject('IFieldGroupsService') private IFieldGroupsService: IFieldGroupsService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private responsiveService: ResponsiveService,
    private confirmDialogService: ConfirmDialogService,
    private translate :TranslateService)
  {
  }

  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('FIELD_MANAGEMENT.MANAGE_FIELD_GROUP.SETUP'),
      relativeUrl: '../..'
    },
        {
      title: this.translate.instant('FIELD_MANAGEMENT.MANAGE_FIELD_GROUP.BOOKING_FIELDS'),
      relativeUrl: '../'
    },
        {
      title: this.translate.instant("FIELD_MANAGEMENT.MANAGE_FIELD_GROUP.FIELD_GROUPS"),
      relativeUrl: './'
    }
  ];

  ngOnInit()
  {
    const request = new GetFieldGroupsRequest();
    request.FieldGroupTypeID = 0;
    request.FilterExpression = '';

    this.form_search = new GetFieldGroupsRequestForm();
    this.form_search.setValue(request);
    this.getMobileSize();
    this.load(request);
  }

  private load(request: GetFieldGroupsRequest)
  {
    if (!this.model)
    {
      this.model = new GetFieldGroupsResponse();
    }

    this.IFieldGroupsService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.bindFieldGroups(this.model.FieldGroups);
    });
  }
  getMobileSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    const screenWidth = this.responsiveService.screenWidth.getValue();
    this.isMobile = screenSize === 'xs';
    this.isTablet = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
    this.isSmallScreen = screenWidth <= 600;
    this.isMediumScreen = screenWidth <= 960;
  }
  private bindFieldGroups(fieldGroups: FieldGroup[])
  {
    this.datasource = new MatTableDataSource<FieldGroup>(fieldGroups);
    this.datasource.paginator = this.paginator;
    this.datasource.sort = this.sort;
  }

  search(form: GetFieldGroupsRequestForm)
  {
    this.load(form.value);
  }

  deleteFieldGroup(index: number)
  {
    index = (this.paginator.pageIndex * this.paginator.pageSize) + index;
    const fieldGroup: FieldGroup = this.model.FieldGroups[index];

    const dialogData: DialogData = {
      title: 'Delete',
      message: `Delete the ${ fieldGroup.Name } group?`,
      textOkayButton: 'Delete',
      textCancelButton: 'Cancel'
    };

    this.confirmDialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
    {
      if (result)
      {
        const request = new DeleteFieldGroupRequest();
        request.FieldGroupID = fieldGroup.FieldGroupID;

        this.IFieldGroupsService.Delete(request).subscribe(response =>
        {
          this.model.FieldGroups.splice(index, 1);
          this.bindFieldGroups(this.model.FieldGroups);
        });
      }
    });
  }

  duplicateFieldGroup(index: number)
  {
    index = (this.paginator.pageIndex * this.paginator.pageSize) + index;
    const fieldGroup: FieldGroup = this.model.FieldGroups[index];

    const dialogData: DialogData = {
      title: 'Duplicate',
      message: `Duplicate the ${ fieldGroup.Name } group?`,
      textOkayButton: 'Duplicate',
      textCancelButton: 'Cancel'
    };

    this.confirmDialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
    {
      if (result)
      {
        const request = new DuplicateFieldGroupRequest();
        request.FieldGroupID = fieldGroup.FieldGroupID;

        this.IFieldGroupsService.Duplicate(request).subscribe(response =>
        {
          this.router.navigate(['manage', response.FieldGroupID], { relativeTo: this.activatedRoute });
        });
      }
    });
  }
}
