export class GetManageMasterFieldRequest
{
  public MasterFieldID?: number;
}

export class GetManageMasterFieldResponse
{
  public MasterFieldID: number;
  public MasterFieldTypes: MasterFieldType[];
  public MasterFieldFormats: MasterFieldFormat[];
  public MasterFieldOptionDecisions: MasterFieldOptionDecision[];
  public MasterFields: MasterField[];
  public SelectedMasterFieldTypeID: number;
  public SelectedMasterFieldFormat: MasterFieldFormat;
  public Title: string;
  public Label: string;
  public FieldGroupID?: number;
  public RootMasterFieldID?: number;
  public MasterFieldOptions: MasterFieldOption[];
  public AllowCreate: boolean;
  public AllowEdit: boolean;
  public AllowRemove: boolean;
  public AllowCreateOption: boolean;
  public AllowEditOption: boolean;
  public AllowRemoveOption: boolean;
}

class MasterFieldType
{
  public MasterFieldTypeID: number;
  public Name: string;
}

export class MasterFieldFormat
{
  public MasterFieldFormatID: number;
  public Name: string;
  public HasOptions: boolean;
  public DefaultOptions: string[];
  public AllowChangeOptions: boolean;
  public MasterFieldTypes?: number[];
}

export class MasterField
{
  public MasterFieldID: number;
  public Title: string;
}

export class MasterFieldOption
{
  public MasterFieldOptionID?: number;
  public Name: string;
  public Decision?: number;
  public DecisionMasterField: MasterField;
  public DecisionMessage: string;
}

class MasterFieldOptionDecision
{
  public MasterFieldOptionDecisionID: number;
  public Decision: string;
}

export class MasterFieldOptionMetadata
{
  public readonly OptionsColumns: string[] = ['Name', 'actions'];
  public readonly ConditionsColumns: string[] = ['Answer', 'Then'];
}

class Decision
{
  public readonly ContinuePerUsual: number = 1;
  public readonly DisplayNewField: number = 2;
  public readonly Disqualify: number = 3;
}

export const DECISION = new Decision();
