import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { MatLegacyPaginator as MatPaginator } from '@angular/material/legacy-paginator';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
import { MatSort } from '@angular/material/sort';
import { ActivatedRoute, Router } from '@angular/router';
import { ResponsiveService } from '@tei/core';
import { BreadCrumb, ConfirmDialogService, DialogData } from '@tei/material';
import { DeleteMasterFieldRequest, DuplicateMasterFieldRequest, GetMasterListRequest, GetMasterListRequestForm, GetMasterListResponse, IMasterListService, MasterField, MasterFieldMetadata } from '../../@intermediate/master-list';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'tei-field-management-pages-master-list',
  templateUrl: './master-list.component.html',
  styleUrls: ['./master-list.component.scss']
})
export class MasterListComponent implements OnInit
{
  model: GetMasterListResponse;
  metadata = new MasterFieldMetadata();
  form_search: GetMasterListRequestForm;
  datasource: MatTableDataSource<MasterField>;

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  isMobile: boolean;
  isTablet: boolean;
  isSmallScreen: boolean;
  isMediumScreen: boolean;

    breadCrumbData: BreadCrumb[] = [
      {
        title: this.translate.instant('FIELD_MANAGEMENT.MASTER_FIELD_LIST.SETUP'),
        relativeUrl: '../..'
      },
          {
        title: this.translate.instant('FIELD_MANAGEMENT.MASTER_FIELD_LIST.BOOKING_FIELDS'),
        relativeUrl: '../'
      },
          {
        title: this.translate.instant("FIELD_MANAGEMENT.MASTER_FIELD_LIST.MASTER_FIELD_LIST"),
        relativeUrl: './'
      }
    ];

  constructor(
    @Inject('IMasterListService') private IMasterListService: IMasterListService,
    private router: Router,
    private activedRoute: ActivatedRoute,
    private responsiveService: ResponsiveService,
    private confirmdialogService: ConfirmDialogService,
    private translate: TranslateService)
  {
  }

  ngOnInit(): void
  {
    const request = new GetMasterListRequest();
    request.MasterFieldTypeID = 0;
    request.FilterExpression = '';
    this.getMobileSize();
    this.form_search = new GetMasterListRequestForm();
    this.form_search.setValue(request);

    this.load(request);
  }

  private load(request: GetMasterListRequest): void
  {
    if (!this.model)
    {
      this.model = new GetMasterListResponse();
    }

    this.IMasterListService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.bindMasterFields(this.model.MasterFields);
    });
  }

  private bindMasterFields(masterFields: MasterField[]): void
  {
    this.datasource = new MatTableDataSource<MasterField>(masterFields);
    this.datasource.paginator = this.paginator;
    this.datasource.sort = this.sort;
  }

  search(form: GetMasterListRequestForm): void
  {
    this.load(form.value);
  }
  getMobileSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    const screenWidth = this.responsiveService.screenWidth.getValue();
    this.isMobile = screenSize === 'xs';
    this.isTablet = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
    this.isSmallScreen = screenWidth <= 600;
    this.isMediumScreen = screenWidth <= 960;
  }
  deleteMasterField(index: number)
  {
    index = (this.paginator.pageIndex * this.paginator.pageSize) + index;
    const masterField: MasterField = this.model.MasterFields[index];

    const dialogData: DialogData = {
      title: 'Delete',
      message: `Delete the ${ masterField.Title } field?`,
      textOkayButton: 'Delete',
      textCancelButton: 'Cancel'
    };

    this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
    {
      if (result)
      {
        const request = new DeleteMasterFieldRequest();
        request.MasterFieldID = masterField.MasterFieldID;

        this.IMasterListService.Delete(request).subscribe(response =>
        {
          this.model.MasterFields.splice(index, 1);
          this.bindMasterFields(this.model.MasterFields);
        });
      }
    });
  }

  duplicateMasterField(index: number): void
  {
    index = (this.paginator.pageIndex * this.paginator.pageSize) + index;
    const masterField: MasterField = this.model.MasterFields[index];

    const dialogData: DialogData = {
      title: 'Duplicate',
      message: `Duplicate the ${ masterField.Title } field?`,
      textOkayButton: 'Duplicate',
      textCancelButton: 'Cancel'
    };

    this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
    {
      if (result)
      {
        const request = new DuplicateMasterFieldRequest();
        request.MasterFieldID = masterField.MasterFieldID;

        this.IMasterListService.Duplicate(request).subscribe(response =>
        {
          this.router.navigate(['manage', response.MasterFieldID], { relativeTo: this.activedRoute });
        });
      }
    });
  }
}
