import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RouteData } from '@tei/core';
import { FieldManagementComponent } from './field-management.component';
import { FieldGroupsComponent } from './pages/field-groups/field-groups.component';
import { ManageFieldGroupComponent } from './pages/manage-field-group/manage-field-group.component';
import { ManageMasterFieldComponent } from './pages/manage-master-field/manage-master-field.component';
import { MasterListComponent } from './pages/master-list/master-list.component';
import { PagesComponent } from './pages/pages.component';
import { SettingsComponent } from './pages/settings/settings.component';
import { TestFieldGroupLookupComponent } from './pages/test-field-group-lookup/test-field-group-lookup.component';

const routes: Routes = [{
  path: '',
  component: FieldManagementComponent,
  children: [
        {
          path: 'master-list',
          component: MasterListComponent,
          data: { RouteData: new RouteData({ Title: 'Field Management' }) },
        },
        {
          path: 'field-groups',
          component: FieldGroupsComponent,
          data: { RouteData: new RouteData({ Title: 'Field Management' }) },
        },
        {
          path: 'test-field-group-lookup',
          component: TestFieldGroupLookupComponent,
          data: { RouteData: new RouteData({ Title: 'Field Management' }) },
        },
        {
          path: 'settings',
          component: SettingsComponent,
          data: { RouteData: new RouteData({ Title: 'Settings' }) }
        },
        {
          path: 'master-list/manage',
          redirectTo: 'master-list/manage/'
        },
        {
          path: 'master-list/manage/:master-field-id',
          component: ManageMasterFieldComponent,
          data: { RouteData: new RouteData({ Title: 'Manage Master Field', CanGoBack: true, BackRoute: 'master-list' }) }
        },
        {
          path: 'master-list/customize/:master-field-id/:field-group-id',
          component: ManageMasterFieldComponent,
          data: { RouteData: new RouteData({ Title: 'Customize Master Field', CanGoBack: true, BackRoute: 'field-groups/manage/:field-group-id' }) }
        },
        {
          path: 'field-groups/manage',
          redirectTo: 'field-groups/manage/'
        },
        {
          path: 'field-groups/manage/:field-group-id',
          component: ManageFieldGroupComponent,
          data: { RouteData: new RouteData({ Title: 'Manage Field Group', CanGoBack: true, BackRoute: 'field-groups' }) }
        }
      ],
    },
    { path: '**', redirectTo: '' },
  ];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class FieldManagementRoutingModule
{
}
