import { UntypedFormGroup, UntypedFormControl } from '@angular/forms';

export class GetFieldGroupsRequest
{
  public FieldGroupTypeID: number;
  public FilterExpression: string;
}

export class GetFieldGroupsRequestForm extends UntypedFormGroup
{
  constructor()
  {
    super({
      FieldGroupTypeID: new UntypedFormControl(''),
      FilterExpression: new UntypedFormControl(''),
    });
  }

  public value: GetFieldGroupsRequest;

  public setValue(value: GetFieldGroupsRequest)
  {
    super.setValue(value);
  }
}

export class GetFieldGroupsResponse
{
  public FieldGroupTypes: FieldGroupType[];
  public FieldGroups: FieldGroup[];
}

class FieldGroupType
{
  public FieldGroupTypeID: number;
  public Name: string;
}

export class FieldGroup
{
  public FieldGroupID: number;
  public Name: string;
  public TypeName: string;
  public InUse: boolean;
  public AllowCreateField: boolean;
  public AllowEditField: boolean;
  public AllowRemoveField: boolean;
  public AllowCustomize: boolean;
}

export class FieldGroupMetadata
{
  public readonly Columns: string[] = ['Name', 'TypeName', 'InUse', 'actions'];
}
