import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import
{
  DeleteFieldGroupRequest,
  DeleteFieldGroupResponse,
  DuplicateFieldGroupRequest,
  DuplicateFieldGroupResponse, GetFieldGroupsRequest,
  GetFieldGroupsResponse, IFieldGroupsService
} from '../@intermediate/field-groups';

@Injectable({
  providedIn: 'root'
})
export class FieldGroupsService implements IFieldGroupsService
{
  protected readonly serviceUrl: string;
  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/FieldManagement/FieldGroups';
  }

  public Get(request: GetFieldGroupsRequest): Observable<GetFieldGroupsResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetFieldGroupsResponse>(apiUrl, { params: request as any });
  }

  public Delete(request: DeleteFieldGroupRequest): Observable<DeleteFieldGroupResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.delete<DeleteFieldGroupResponse>(apiUrl, { params: request as any });
  }

  public Duplicate(request: DuplicateFieldGroupRequest): Observable<DuplicateFieldGroupResponse>
  {
    const apiUrl = this.serviceUrl + '/Duplicate';
    return this.http.post<DuplicateFieldGroupResponse>(apiUrl, request);
  }
}
