import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { FieldManagementRoutingModule } from './field-management-routing.module';
import { FieldManagementComponent } from './field-management.component';
import { DefaultModule } from './pages/default/default.module';
import { FieldGroupsModule } from './pages/field-groups/field-groups.module';
import { ManageFieldGroupModule } from './pages/manage-field-group/manage-field-group.module';
import { ManageMasterFieldModule } from './pages/manage-master-field/manage-master-field.module';
import { MasterListModule } from './pages/master-list/master-list.module';
import { PagesModule } from './pages/pages.module';
import { SettingsModule } from './pages/settings/settings.module';
import { TestFieldGroupLookupModule } from './pages/test-field-group-lookup/test-field-group-lookup.module';

const BASE_MODULES = [RouterModule, CommonModule, FormsModule, ReactiveFormsModule];
const MODULES = [PagesModule,
  DefaultModule,
  MasterListModule,
  FieldGroupsModule,
  TestFieldGroupLookupModule,
  SettingsModule,
  ManageMasterFieldModule,
  ManageFieldGroupModule,
  FieldManagementRoutingModule
];

const COMPONENTS = [FieldManagementComponent];
const PIPES = [];

@NgModule({
  imports: [...BASE_MODULES, ...MODULES],
  exports: [...BASE_MODULES, ...MODULES, ...COMPONENTS, ...PIPES],
  declarations: [...COMPONENTS, ...PIPES],
  providers: []
})
export class FieldManagementModule { }
