import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetManageMasterFieldRequest, GetManageMasterFieldResponse, IManageMasterFieldService, PostManageMasterFieldRequest, PostManageMasterFieldResponse } from '../@intermediate/manage-master-field';

@Injectable({
  providedIn: 'root'
})

export class ManageMasterFieldService implements IManageMasterFieldService
{
  protected readonly serviceUrl: string;
  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/FieldManagement/ManageMasterField';
  }

  public Get(request: GetManageMasterFieldRequest): Observable<GetManageMasterFieldResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetManageMasterFieldResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostManageMasterFieldRequest): Observable<PostManageMasterFieldResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostManageMasterFieldResponse>(apiUrl, request);
  }
}
