import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { DeleteMasterFieldRequest, DeleteMasterFieldResponse, DuplicateMasterFieldRequest, DuplicateMasterFieldResponse, GetMasterListRequest, GetMasterListResponse, IMasterListService } from '../@intermediate/master-list';

@Injectable({
  providedIn: 'root'
})

export class MasterListService implements IMasterListService
{
  protected readonly serviceUrl: string;
  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/FieldManagement/MasterList';
  }

  public Get(request: GetMasterListRequest): Observable<GetMasterListResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetMasterListResponse>(apiUrl, { params: request as any });
  }

  public Delete(request: DeleteMasterFieldRequest): Observable<DeleteMasterFieldResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.delete<DeleteMasterFieldResponse>(apiUrl, { params: request as any });
  }

  public Duplicate(request: DuplicateMasterFieldRequest): Observable<DuplicateMasterFieldResponse>
  {
    const apiUrl = this.serviceUrl + '/Duplicate';
    return this.http.post<DuplicateMasterFieldResponse>(apiUrl, request);
  }
}
