import { UntypedFormGroup, UntypedFormControl } from '@angular/forms';

export class GetMasterListRequest
{
  public MasterFieldTypeID: number;
  public FilterExpression: string;
}

export class GetMasterListRequestForm extends UntypedFormGroup
{
  constructor()
  {
    super({
      MasterFieldTypeID: new UntypedFormControl(''),
      FilterExpression: new UntypedFormControl(''),
    });
  }

  public value: GetMasterListRequest;

  public setValue(value: GetMasterListRequest)
  {
    super.setValue(value);
  }
}

export class GetMasterListResponse
{
  public MasterFieldTypes: MasterFieldType[];
  public MasterFields: MasterField[];
}

class MasterFieldType
{
    public MasterFieldTypeID: number;
    public Name: string;
}

export class MasterField
{
    public MasterFieldID: number;
    public TypeName: string;
    public Title: string;
    public Label: string;
    public FormatName: string;
    public InUse: boolean;
    public AllowCreate: boolean;
    public AllowEdit: boolean;
    public AllowRemove: boolean;
}

export class MasterFieldMetadata
{
  public readonly Columns: string[] = ['TypeName', 'Title', 'Label', 'FormatName', 'InUse', 'actions'];
}
