import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetManageFieldGroupRequest, GetManageFieldGroupResponse, IManageFieldGroupService, PostManageFieldGroupRequest, PostManageFieldGroupResponse } from '../@intermediate/manage-field-group';

@Injectable({
  providedIn: 'root'
})

export class ManageFieldGroupService implements IManageFieldGroupService
{
  protected readonly serviceUrl: string;
  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/FieldManagement/ManageFieldGroup';
  }

  public Get(request: GetManageFieldGroupRequest): Observable<GetManageFieldGroupResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetManageFieldGroupResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostManageFieldGroupRequest): Observable<PostManageFieldGroupResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostManageFieldGroupResponse>(apiUrl, request);
  }
}
