import { UntypedFormGroup, UntypedFormControl, UntypedFormArray, Validators } from '@angular/forms';
import { GetManageFieldGroupResponse, FieldGroupMasterField, SCOPE } from './get-manage-field-group.model';

export class PostManageFieldGroupRequest
{
  public FieldGroupID?: number;
  public Name: string;
  public SelectedFieldGroupTypeID: number;
  public FieldGroupMasterFields: FieldGroupMasterField[];
}

export class PostManageFieldGroupRequestForm extends UntypedFormGroup
{
  public readonly FieldGroupMasterFields: UntypedFormArray;

  constructor(private addMode: boolean)
  {
    super({
      FieldGroupID: new UntypedFormControl(''),
      Name: new UntypedFormControl('', Validators.required),
      SelectedFieldGroupTypeID: new UntypedFormControl('', Validators.required),
      FieldGroupMasterFields: new UntypedFormArray([], Validators.required)
    });

    this.FieldGroupMasterFields = this.get('FieldGroupMasterFields') as UntypedFormArray;
  }

  public value: PostManageFieldGroupRequest;

  public setValue(value: GetManageFieldGroupResponse)
  {
    const request = new PostManageFieldGroupRequest();

    request.FieldGroupID = value.FieldGroupID;
    request.Name = value.Name;
    request.SelectedFieldGroupTypeID = value.SelectedFieldGroupTypeID;
    request.FieldGroupMasterFields = value.FieldGroupMasterFields;

    this.FieldGroupMasterFields.clear();

    request.FieldGroupMasterFields.forEach((masterFieldMasterField) =>
    {
      this.addMasterField(masterFieldMasterField);
    });

    super.setValue(request);
  }

  public addMasterField(masterFieldMasterField: FieldGroupMasterField)
  {
    const masterFieldMasterFieldForm = new FieldGroupMasterFieldForm(this.addMode);
    masterFieldMasterFieldForm.setValue(masterFieldMasterField);

    this.FieldGroupMasterFields.push(masterFieldMasterFieldForm);
  }

  public isCustomized(): boolean
  {
    if (this.FieldGroupMasterFields.value)
    {
      return (this.FieldGroupMasterFields.getRawValue() as FieldGroupMasterField[]).some(f => !f.UseDefault);
    }
    else
    {
      return false;
    }
  }
}

class FieldGroupMasterFieldForm extends UntypedFormGroup
{
  constructor(private addMode: boolean)
  {
    super({
      FieldGroupMasterFieldID: new UntypedFormControl(''),
      MasterFieldID: new UntypedFormControl('', Validators.required),
      Scope: new UntypedFormControl('', Validators.required),
      Required: new UntypedFormControl('', Validators.required),
      SaveEntry: new UntypedFormControl('', Validators.required),
      HideInEmail: new UntypedFormControl('', Validators.required),
      UseDefault: new UntypedFormControl({ value: '', disabled: addMode }, Validators.required),
    });
  }

  public value: FieldGroupMasterField;

  public setValue(value: FieldGroupMasterField)
  {
    super.patchValue({
      MasterFieldID: value.MasterFieldID,
      Scope: value.Scope,
      Required: value.Required,
      SaveEntry: value.SaveEntry,
      HideInEmail: value.HideInEmail,
      UseDefault: value.UseDefault
    });

    if (!value.UseDefault)
    {
      this.get('UseDefault').disable();
    }
  }
}

export class PostManageFieldGroupResponse
{

}
