import { UntypedFormGroup, UntypedFormControl, UntypedFormArray, Validators } from '@angular/forms';
import { GetManageMasterFieldResponse, MasterFieldOption, MasterFieldFormat, DECISION } from './get-manage-master-field.model';

export class PostManageMasterFieldRequest
{
  public MasterFieldID?: number;
  public SelectedMasterFieldTypeID: number;
  public SelectedMasterFieldFormat: MasterFieldFormat;
  public Title: string;
  public Label: string;
  public FieldGroupID?: number;
  public RootMasterFieldID?: number;
  public MasterFieldOptions: MasterFieldOption[];
}

export class PostManageMasterFieldRequestForm extends UntypedFormGroup
{
  public readonly MasterFieldOptions: UntypedFormArray;

  constructor()
  {
    super({
      MasterFieldID: new UntypedFormControl(''),
      SelectedMasterFieldTypeID: new UntypedFormControl({ value: '', disabled: true }, Validators.required),
      SelectedMasterFieldFormat: new UntypedFormControl('', Validators.required),
      Title: new UntypedFormControl('', Validators.required),
      Label: new UntypedFormControl('', Validators.required),
      FieldGroupID: new UntypedFormControl(''),
      RootMasterFieldID: new UntypedFormControl(''),
      MasterFieldOptions: new UntypedFormArray([])
    });

    this.MasterFieldOptions = this.get('MasterFieldOptions') as UntypedFormArray;
  }

  public value: PostManageMasterFieldRequest;

  public setValue(value: GetManageMasterFieldResponse)
  {
    const request = new PostManageMasterFieldRequest();

    request.MasterFieldID = value.MasterFieldID;
    request.SelectedMasterFieldTypeID = value.SelectedMasterFieldTypeID;
    request.SelectedMasterFieldFormat = value.SelectedMasterFieldFormat;
    request.Title = value.Title;
    request.Label = value.Label;
    request.FieldGroupID = value.FieldGroupID;
    request.RootMasterFieldID = value.RootMasterFieldID;
    request.MasterFieldOptions = value.MasterFieldOptions;

    this.MasterFieldOptions.clear();

    request.MasterFieldOptions.forEach((masterFieldOption) =>
    {
      this.addOption(masterFieldOption);
    });

    super.setValue(request);

    this.get('SelectedMasterFieldFormat').valueChanges.subscribe(value =>
    {
      const masterFieldFormat = value as MasterFieldFormat;

      if (masterFieldFormat && masterFieldFormat.HasOptions)
      {
        this.MasterFieldOptions.setValidators([Validators.required]);
        this.MasterFieldOptions.updateValueAndValidity();
      }
      else
      {
        this.MasterFieldOptions.clearValidators();
        this.MasterFieldOptions.updateValueAndValidity();
      }
    });
  }

  public addOption(masterFieldOption: MasterFieldOption)
  {
    const masterFieldOptionForm = new MasterFieldOptionForm();
    masterFieldOptionForm.setValue(masterFieldOption);

    this.MasterFieldOptions.push(masterFieldOptionForm);
  }
}

class MasterFieldOptionForm extends UntypedFormGroup
{
  constructor()
  {
    super({
      MasterFieldOptionID: new UntypedFormControl(''),
      Name: new UntypedFormControl('', Validators.required),
      Decision: new UntypedFormControl('', Validators.required),
      DecisionMasterField: new UntypedFormControl(''),
      DecisionMessage: new UntypedFormControl(''),
    });
  }

  public value: MasterFieldOption;

  public setValue(value: MasterFieldOption)
  {
    super.setValue(value);

    this.get('Decision').valueChanges.subscribe(value =>
    {
      const decision = value as number;

      this.get('DecisionMasterField').setValue(null);
      this.get('DecisionMessage').setValue('');

      this.get('DecisionMasterField').clearValidators();
      this.get('DecisionMasterField').updateValueAndValidity();

      this.get('DecisionMessage').clearValidators();
      this.get('DecisionMessage').updateValueAndValidity();

      if (decision === DECISION.DisplayNewField)
      {
        this.get('DecisionMasterField').setValidators([Validators.required]);
        this.get('DecisionMasterField').updateValueAndValidity();
      }
      else if (decision === DECISION.Disqualify)
      {
        this.get('DecisionMessage').setValidators([Validators.required]);
        this.get('DecisionMessage').updateValueAndValidity();
      }
    });

    this.get('DecisionMasterField').valueChanges.subscribe(value =>
    {
      const decision = this.get('Decision').value;

      if(decision === DECISION.DisplayNewField && value && !value.MasterFieldID)
      {
        this.get('DecisionMasterField').setErrors({'incorrect': true});
      }
    });
  }
}

export class PostManageMasterFieldResponse
{

}
