import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
import { ActivatedRoute, Router } from '@angular/router';
import { ConfirmDialogService, DialogData, SnackBarService } from '@tei/material';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { baseRoute } from '../../@intermediate/constants/configs';
import { DECISION, GetManageMasterFieldRequest, GetManageMasterFieldResponse, IManageMasterFieldService, MasterField, MasterFieldFormat, MasterFieldOption, MasterFieldOptionMetadata, PostManageMasterFieldRequest, PostManageMasterFieldRequestForm } from '../../@intermediate/manage-master-field';

@Component({
  selector: 'tei-field-management-pages-manage-master-field',
  templateUrl: './manage-master-field.component.html',
  styleUrls: ['./manage-master-field.component.scss']
})
export class ManageMasterFieldComponent implements OnInit
{
  model: GetManageMasterFieldResponse;
  form: PostManageMasterFieldRequestForm;
  optionMetadata = new MasterFieldOptionMetadata();
  datasource_options: MatTableDataSource<MasterFieldOption>;
  filteredMasterFields: Observable<MasterField[]>[];
  readonly decision: any = DECISION;

  masterFieldID: number;
  fieldGroupID: number;
  optionName: string;
  optionRowIndex: number = -1;
  manageFieldBaseRoute = baseRoute;

  backRoute: any[] = [baseRoute + '/booking-fields/master-field-list'];

  constructor(
    @Inject('IManageMasterFieldService') private ManageMasterFieldService: IManageMasterFieldService,
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private confirmdialogService: ConfirmDialogService,
    private snackbarService: SnackBarService)
  {
    activatedRoute.params.subscribe(params =>
    {
      if (params['master-field-id'] && params['master-field-id'] !== '')
      {
        this.masterFieldID = params['master-field-id'];
      }

      if (params['field-group-id'] && params['field-group-id'] !== '')
      {
        this.fieldGroupID = params['field-group-id'];
        this.backRoute = [baseRoute + '/field-groups/manage', this.fieldGroupID];
      }
    });
  }

  ngOnInit()
  {
    this.form = new PostManageMasterFieldRequestForm();
    const request = new GetManageMasterFieldRequest();

    if (this.masterFieldID)
    {
      request.MasterFieldID = this.masterFieldID;
    }

    this.load(request);
  }

  private load(request: GetManageMasterFieldRequest)
  {
    this.model = new GetManageMasterFieldResponse();

    this.ManageMasterFieldService.Get(request).subscribe(response =>
    {
      this.model = response;

      if (this.fieldGroupID)
      {
        this.model.FieldGroupID = this.fieldGroupID;
      }

      if (response.SelectedMasterFieldFormat && response.MasterFieldFormats && response.MasterFieldFormats.length !== 0)
      {
        response.SelectedMasterFieldFormat = response.MasterFieldFormats.find(f => response.SelectedMasterFieldFormat.MasterFieldFormatID == f.MasterFieldFormatID);
      }

      if (!this.masterFieldID && this.model.MasterFieldTypes.length !== 1)
      {
        this.form.get('SelectedMasterFieldTypeID').enable();
      }

      this.bindForm(this.model);
    });
  }

  private bindForm(response: GetManageMasterFieldResponse)
  {
    this.form.setValue(response);
    this.bindOptions(this.model.MasterFieldOptions);
    this.DisableTitleAndFormatForProfileCategory(response.SelectedMasterFieldTypeID);
    this.DisableFormatInEditMode(!!response.MasterFieldID);

    this.form.get('SelectedMasterFieldFormat').valueChanges.subscribe(value =>
    {
      const masterFieldFormat = value as MasterFieldFormat;

      this.model.MasterFieldOptions = [];
      this.bindOptions(this.model.MasterFieldOptions);
      this.form.MasterFieldOptions.clear();

      if (masterFieldFormat.DefaultOptions)
      {
        masterFieldFormat.DefaultOptions.forEach((optionName) =>
        {
          this.addOption(optionName);
        });
      }
    });
  }

  private DisableFormatInEditMode(isEditMode: boolean): void
  {
    if (isEditMode)
    {
      this.form.get('SelectedMasterFieldFormat').disable();
    }
  }

  private DisableTitleAndFormatForProfileCategory(id): void
  {
    if (id == 4)
    {
      this.form.get('SelectedMasterFieldFormat').disable();
      this.form.get('Title').disable();
    }
  }

  private _filterMasterFields(value: string): MasterField[]
  {
    return this.model.MasterFields.filter(masterField => masterField.Title.toLowerCase().includes(value.toLowerCase()));
  }

  displayMasterField(masterField?: MasterField): string | undefined
  {
    return masterField ? masterField.Title : undefined;
  }

  manageOption()
  {
    if (this.optionRowIndex === -1)
    {
      this.addOption(this.optionName);
    }
    else
    {
      this.editOption(this.optionRowIndex, this.optionName);
    }

    this.bindOptions(this.model.MasterFieldOptions);

    this.optionName = null;
    this.optionRowIndex = -1;
  }

  private bindOptions(masterFieldOptions: MasterFieldOption[])
  {
    this.datasource_options = new MatTableDataSource<MasterFieldOption>(masterFieldOptions);

    this.filteredMasterFields = [];

    this.form.MasterFieldOptions.controls.forEach((control) =>
    {
      this.filteredMasterFields.push(control.get('DecisionMasterField').valueChanges
        .pipe(
          startWith(''),
          map(value =>
          {
            return value && value.length >= 1 ? this._filterMasterFields(value) : [];
          })
        ));
    });
  }

  private addOption(optionName: string)
  {
    const masterFieldOption = new MasterFieldOption();

    masterFieldOption.MasterFieldOptionID = null;
    masterFieldOption.Name = optionName;
    masterFieldOption.Decision = 1;
    masterFieldOption.DecisionMasterField = null;
    masterFieldOption.DecisionMessage = null;

    this.model.MasterFieldOptions.push(masterFieldOption);
    this.form.addOption(masterFieldOption);
  }

  initiateEditOption(index: number)
  {
    this.optionName = this.model.MasterFieldOptions[index].Name;
    this.optionRowIndex = index;
  }

  private editOption(optionRowIndex: number, optionName: string)
  {
    this.model.MasterFieldOptions[optionRowIndex].Name = optionName;
    this.form.MasterFieldOptions.controls[optionRowIndex].setValue(this.model.MasterFieldOptions[this.optionRowIndex]);
  }

  moveOptionUp(index: number)
  {
    this.moveOption(index, index - 1);
  }

  moveOptionDown(index: number)
  {
    this.moveOption(index, index + 1);
  }

  private moveOption(from: number, to: number)
  {
    this.moveArrayItem(this.form.MasterFieldOptions.controls, from, to);
    this.moveArrayItem(this.model.MasterFieldOptions, from, to);
    this.bindOptions(this.model.MasterFieldOptions);
  }

  deleteOption(index: number)
  {
    const masterFieldOption = this.model.MasterFieldOptions[index];

    const dialogData: DialogData = {
      title: 'Delete',
      message: `Delete ${ masterFieldOption.Name }?`,
      textOkayButton: 'Delete',
      textCancelButton: 'Cancel'
    };

    this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.form.MasterFieldOptions.controls.splice(index, 1);
        this.form.MasterFieldOptions.updateValueAndValidity();

        this.model.MasterFieldOptions.splice(index, 1);
        this.bindOptions(this.model.MasterFieldOptions);
      }
    });
  }

  private moveArrayItem(array: any[], from: number, to: number)
  {
    array.splice(to, 0, array.splice(from, 1)[0]);
  }

  save(form: PostManageMasterFieldRequestForm)
  {
    const request = form.getRawValue() as PostManageMasterFieldRequest;

    this.ManageMasterFieldService.Post(request).subscribe(
      response =>
      {
        this.snackbarService.showSuccess(`${ request.Title } saved successfully.`);
        this.router.navigate(this.backRoute, { relativeTo: this.activatedRoute });
      },
      ex =>
      {
        this.snackbarService.showError(ex.error.Message);
      });
  }

  GetMasterFieldFormats(form: PostManageMasterFieldRequestForm)
  {
    if (form.controls.SelectedMasterFieldTypeID.value)
    {
      return this.model.MasterFieldFormats.filter(f => f.MasterFieldTypes && f.MasterFieldTypes?.includes(form.controls.SelectedMasterFieldTypeID.value));
    }
  }
}
