import { Component, Inject, OnInit } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { IFieldGroupLookupService, IFieldGroupLookupWrapperService, PostFieldGroupLookupWrapperRequest } from '@tei/common-field-management-shared';

@Component({
  selector: 'tei-field-management-pages-test-field-group-lookup',
  templateUrl: './test-field-group-lookup.component.html',
  styleUrls: ['./test-field-group-lookup.component.scss']
})
export class TestFieldGroupLookupComponent implements OnInit
{
  formCreate: UntypedFormGroup = new UntypedFormGroup({
    FieldGroupLookup: new UntypedFormControl()
  });

  formEdit: UntypedFormGroup = new UntypedFormGroup({
    FieldGroupLookup: new UntypedFormControl()
  });

  formCreatePublic: UntypedFormGroup = new UntypedFormGroup({
    FieldGroupLookup: new UntypedFormControl()
  });

  formEditPublic: UntypedFormGroup = new UntypedFormGroup({
    FieldGroupLookup: new UntypedFormControl()
  });

  constructor(
    @Inject('IFieldGroupLookupService') private FieldGroupLookupService: IFieldGroupLookupService,
    @Inject('IFieldGroupLookupWrapperService') private FieldGroupLookupWrapperService: IFieldGroupLookupWrapperService
  )
  {

  }

  ngOnInit()
  {
    this.formEditPublic.disable();
  }

  submit(form: UntypedFormGroup)
  {
    if (form.valid)
    {

      const request: PostFieldGroupLookupWrapperRequest = form.value.FieldGroupLookup;

      this.FieldGroupLookupWrapperService.Post(request).subscribe(response =>
      {
      });
    }
  }

  onFieldGroupLookupLoaded($event)
  {
  }

  onMasterFieldValueChanged($event)
  {
  }
}
