import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';

import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyAutocompleteModule as MatAutocompleteModule } from '@angular/material/legacy-autocomplete';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyPaginatorModule as MatPaginatorModule } from '@angular/material/legacy-paginator';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacySlideToggleModule as MatSlideToggleModule } from '@angular/material/legacy-slide-toggle';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { ResponsiveTableHeightModule } from '@tei/core';
import { ConfirmDialogModule, SnackBarModule } from '@tei/material';
import { SettingsCardLayoutModule } from '@tei/theme';
import { ManageFieldGroupService } from '../../@services/manage-field-group.service';
import { ManageFieldGroupComponent } from './manage-field-group.component';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatCardModule, MatDividerModule, MatInputModule, MatSelectModule,
  MatButtonToggleModule, MatIconModule, MatButtonModule, MatTableModule, MatPaginatorModule,
  MatAutocompleteModule, MatSlideToggleModule, MatMenuModule];
const TEI_MATERIAL_MODULES = [SnackBarModule, ConfirmDialogModule, SettingsCardLayoutModule];
const TEI_CORE_MODULES = [ResponsiveTableHeightModule];
const TEI_PROVIDERS = [];
@NgModule({
  declarations: [ManageFieldGroupComponent],
  imports: [
    RouterModule.forChild([{ path: '', component: ManageFieldGroupComponent }]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES
  ],
  providers: [
    { provide: 'IManageFieldGroupService', useClass: ManageFieldGroupService },
    ...TEI_PROVIDERS
  ],
})
export class ManageFieldGroupModule { }
